export const TIME_REGEX = /^(0[0-9]|1[0-9]|2[0-3]):[0-5][0-9]$/;

const emptyArray: never[] = [];

export const getTimes = (timeStr?: string): string[] => {
  if (timeStr && TIME_REGEX.test(timeStr)) {
    return timeStr.split(':');
  }

  return emptyArray;
};

export const scrollToTop = (containerRef: React.RefObject<HTMLUListElement | null>, target: HTMLElement): void => {
  containerRef.current?.scroll({ top: target.offsetTop, behavior: 'smooth' });
};
